.topbar {
    width: 100%;
    height: 50px;
    background-color: rgb(251, 233, 198);
    position: sticky;
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.topbarLeft{
    font-weight: bold;
    font-size: 30px;
    color:darkblue;
}
.topbarRight{
    font-weight: bold;
    font-size: 30px;
    color:darkblue;
}